import React, { useState } from 'react';
import Layout from '../components/layout';
// Gatsby supports TypeScript natively!
import { PageProps, Link, graphql } from "gatsby"

import SEO from "../components/seo"
import DisplayTag from "../components/DisplayTag"
import { rhythm } from "../utils/typography"

type Data = {
    site: {
        siteMetadata: {
            title: string
        }
    }
    allMarkdownRemark: {
        edges: {
            node: {
                excerpt: string
                frontmatter: {
                    title: string
                    date: string
                    description: string
                }
                fields: {
                    slug: string
                }
            }
        }[]
    }
}

const BlogIndex = ({ data: {
    allMarkdownRemark: { group, edges },
    site: {
        siteMetadata: { title },
    },
}, location }: PageProps<Data>) => {
    const posts = edges
    const [filter, setFilter] = useState('')
    const handleClick = (e) => {
        if (!e.target || !e.target.textContent) console.log('nothing selected');
        const str = e.target.textContent;
        const tag = str.substr(0, str.indexOf(' ('))
        setFilter(tag);
    }
    const arr = filter ? edges.filter((elem) => elem.node.frontmatter.tags.includes(filter)) : posts
    return (
        <Layout location={location} title={title}>
            <DisplayTag group={group} clickHandler={handleClick} />
            <SEO title="All posts" />
            {arr.map(({ node }) => {
                const title = node.frontmatter.title || node.fields.slug
                return (
                    <article key={node.fields.slug}>
                        <header>
                            <h3
                                style={{
                                    margin: 0,
                                    fontFamily: 'sans-serif',
                                    fontSize: '1.2rem',
                                    display: 'flex',
                                    justifyContent: 'space-between',
                                }}
                            >
                                <Link style={{ boxShadow: `none` }} to={node.fields.slug}>
                                    {title}
                                </Link>
                                <small style={{ fontWeight: 500, fontStyle: 'italic', }}>{node.frontmatter.date}</small>
                            </h3>
                        </header>
                    </article>
                )
            })}
        </Layout >
    )
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
        group(field: frontmatter___tags) {
            fieldValue
            totalCount
          }        
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
            tags
          }
        }
      }
    }
  }
`